/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css?family=Athiti&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1;
  box-sizing: border-box;
  background: linear-gradient(#f8d2d2, #cfd2f4);
  display: flex;
  flex-direction: column; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

header {
  margin: 0 auto; }

ul {
  display: flex;
  font-family: 'Athiti';
  text-transform: uppercase;
  font-size: 20px; }
  ul li a {
    color: #000000;
    text-decoration: none; }
    ul li a:visited {
      color: #000000; }

html, body {
  font-size: 16px; }

.title-animated {
  animation: typewriter 5s steps(40) 1s 1 normal both, blinkTextCursor 500ms steps(40) infinite normal;
  animation-iteration-count: infinite;
  overflow: hidden;
  white-space: nowrap;
  color: black;
  border-right: 2px solid rgba(255, 255, 255, 0.75); }

@keyframes typewriter {
  from {
    width: 0; }
  to {
    width: 16em; } }

@keyframes blinkTextCursor {
  from {
    border-right-color: rgba(255, 255, 255, 0.75); }
  to {
    border-right-color: transparent; } }

#input-temp {
  border: none;
  -moz-appearance: textfield; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none; }

.calculator {
  display: grid; }
  .calculator span {
    display: flex; }

.unit input[type="number"] {
  -moz-appearance: textfield; }

.unit input::-webkit-outer-spin-button,
.unit input::-webkit-inner-spin-button {
  -webkit-appearance: none; }

input[type="text"] {
  outline: 0;
  border: none; }

button {
  outline: 0;
  border: none; }

.tasksBoard {
  display: none; }

ul {
  list-style: none; }

a {
  cursor: pointer; }

.delete {
  cursor: pointer; }
